*{
  box-sizing: border-box;
  padding:0;
  margin:0;
  font-family:"Josefin Sans", "Arial Narrow";
  }
  .header{
  /* width:100px; */
  line-height:100px;
  background-color:#f5ba13;
  display:flex;
  justify-content: flex-start;
  align-items:center;
  padding-left:50px;
  color:white;
  box-shadow: 5px 3px 15px -5px black;
  }
  .main-note{
  width:500px;
  padding:15px 10px 5px 10px;
  margin:20px auto;
  }
  .main-notes h1{
  color:#34495e;
  }
  .main-notes p{
  color:grey;
  }
  form{
  min-height:50px;
  color:white;
  padding:15px 10px 5px 10px;
  box-shadow:3px 5px 15px -5px black;
  border-radius:10px;
  position: relative;
  }
input{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
}
textarea{
width: 100%;
line-height: 30px;
background: transparent;
border: none;
outline: none;
font-size: 1rem;
margin: 10px 0;
}
