.note{

  background: #fff;
  border-radius: 7px;
  box-shadow: 0px 2px 5px #ccc; 
  width: 240px;
  padding: 10px;
  margin: 16px;
  float: left;
  text-align: left;

  }
  
  .note h1 {
  font-size: 1.1rem;
  margin-bottom: 6px;
  }
  
  .note p{
  font-size: 1.1rem;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
  }
  
   .btn{
  position:relative;
  float: right;
  margin-right: 10px;
  color: #f4b400;
  border: none;
  width: 50px;
  height: 50px;
  background: transparent;
cursor: pointer;
outline: none;
box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, alpha);
 border-radius: 50%; 
 justify-content: left;
 align-items: left;
}
